<template>
  <div>
    <form @submit.prevent="onSubmit">
      <div></div>
      <input type="text" v-model="accountParams.userId" placeholder="user id">
      <div></div>
      <input type="password" v-model="accountParams.pwd" required placeholder=" ">
      <div> </div>
      <input type="password" v-model="accountParams.pwd_confirm" required placeholder=" ">

      <div></div>
      <input type="text" v-model="accountParams.userName" required placeholder=" ">

      <div>BattleNet ID</div>
      <input type="text" v-model="accountParams.bNetId" required placeholder="  ">

      <div> </div>
      <select v-model="accountParams.tribe" required>
        <option v-for="tribe in tribes" v-bind:value="tribe.val">{{tribe.name}}</option>
      </select>

      <br>
      <br>
      <div> </div>
      <div>APM</div>
      <input type="number" v-model="optionalInfo.apm" placeholder=" ">
      <div>( ) </div>
      <select v-model="optionalInfo.grade" required>
        <option v-for="grade in grades" v-bind:value="grade.val">{{grade.name}}</option>
      </select>
      <div> </div>
      <input type="text" v-model="optionalInfo.comment" placeholder="...">
      <br>
      <button type="submit"style="font-size: 16px; height: 45px"> </button>
      <div class="error-text">{{error}}</div>
    </form>
  </div>
</template>

<script>
export default {
  name: 'CreateAccount',
  data () {
    return {
      accountParams: {
        userId: '',
        pwd: '',
        pwd_confirm: '',
        userName: '',
        bNetId: '',
        tribe: '',
        optionalInfo: {}
      },
      optionalInfo: { apm: '', grade: '', comment: '' },
      tribes: [{name: '', val: 'zerg'}, {name: '', val: 'terran'}, {name: '', val: 'protoss'}, {name: '', val: 'random'} ],
      grades: [{name: 'S', val: 's'}, {name: 'A', val: 'a'}, {name: 'B', val: 'b'}, {name: 'C', val: 'c'}, {name: 'D', val: 'd'}, {name: 'E', val: 'e'}, {name: 'F', val: 'f'},]
    }
  },
  methods: {
    async onSubmit () {
      this.accountParams.optionalInfo = this.optionalInfo
      this.$lcordAPI.user.create(this.accountParams)
        .then(() => {
          this.$router.push({name: 'Login'})
        })
    }
  },
  beforeMount() {

  }
}
</script>

<style scoped>

</style>



